/* Style par défaut pour grands écrans */
#home {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  width: 100%;
  flex-wrap: nowrap;  /* Sur grands écrans, les bannières sont côte à côte */
}

/* Style pour petits écrans */
@media (max-width: 768px) {
  #home {
    flex-direction: column;  /* Sur petits écrans, les bannières se mettent en colonne */
    align-items: center;     /* Centrer les bannières horizontalement */
  }
}

@media (max-width: 768px) {
  .banner-container {
    flex-direction: column;
  }
}


.modal {
  font-size: 14px;
}
.modal > .header {
  width: 100%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.modal > .content img {
  max-width: 85%; /* Réduit la taille de l'image */
  height: auto;
  border-radius: 8px;
  margin-bottom: 15px;
}




.modal > .content .description {
  font-size: 16px;
  color: #333;
  text-align: left; /* Aligne le texte à gauche */
  line-height: 1.5;
  margin-top: 10px;
  padding-left: 20px; /* Ajoute un retrait à gauche */
}


.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}